/* ===== Light, calm system look with stronger responsiveness & a11y ===== */
:root {
  --bg: #f8fafc;
  --card: #ffffff;
  --border: #e6eaf0;
  --text: #0f172a;
  --muted: #5b6b82;
  --primary: #2563eb;
  --primary-600: #1e55cb;
  --primary-50: #eef2ff;
  --danger: #b91c1c;
  --radius: 16px;
  --shadow: 0 8px 24px rgba(15, 23, 42, 0.06);

  --fs-xs: 12px;
  --fs-sm: 13px;
  --fs-base: 15px;
  --fs-lg: 17px;
  --fs-xl: 20px;
  --lh: 1.6;
}

* {
  box-sizing: border-box;
}
html,
body,
#root {
  height: 100%;
}
body {
  margin: 0;
  background: var(--bg);
  color: var(--text);
  font: var(--fs-base) / var(--lh) ui-sans-serif, system-ui, -apple-system,
    Segoe UI, Roboto, Helvetica, Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  min-height: 100%;
  padding: 28px;
}
.shell {
  max-width: 1100px;
  margin: 0 auto;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}
.cardish {
  background: transparent;
  border: none;
  box-shadow: none;
}
.brand {
  display: flex;
  align-items: center;
  gap: 12px;
}
.logo {
  font-size: 22px;
}
.title {
  font-weight: 800;
  font-size: var(--fs-xl);
  letter-spacing: 0.2px;
}
.subtitle {
  color: var(--muted);
  font-size: var(--fs-sm);
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: 22px;
  box-shadow: var(--shadow);
  margin-bottom: 18px;
}
.section-title {
  font-weight: 800;
  font-size: var(--fs-lg);
  margin-bottom: 12px;
}

/* Layout helpers */
.grid {
  display: grid;
  gap: 14px;
}
.grid-2 {
  grid-template-columns: repeat(2, minmax(0, 1fr));
}
@media (max-width: 860px) {
  .grid-2 {
    grid-template-columns: 1fr;
  }
}

.row {
  display: flex;
  align-items: center;
  gap: 10px;
}
.row-between,
.between {
  justify-content: space-between;
}
.end {
  justify-content: flex-end;
}
.gap-8 {
  gap: 8px;
}
.mt-16 {
  margin-top: 16px;
}
.mb-8 {
  margin-bottom: 8px;
}
.mb-12 {
  margin-bottom: 12px;
}
.wrap {
  flex-wrap: wrap;
}

/* Inputs */
.input,
.textarea,
select {
  width: 100%;
  border: 1px solid var(--border);
  background: #fff;
  padding: 12px 14px;
  border-radius: 12px;
  font-size: var(--fs-base);
  outline: none;
  transition: border-color 0.15s ease, box-shadow 0.15s ease;
}
.input:focus,
.textarea:focus,
select:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.15);
}
.textarea {
  min-height: 120px;
  resize: vertical;
}

/* Normalise native select height */
select.input.select {
  appearance: none;
  -webkit-appearance: none;
  line-height: 1.2;
}

/* Buttons */
.btn {
  border: 1px solid var(--border);
  background: #fff;
  color: var(--text);
  padding: 10px 16px;
  border-radius: 999px;
  cursor: pointer;
  transition: all 0.15s ease;
  font-weight: 600;
}
.btn:hover {
  box-shadow: 0 2px 10px rgba(15, 23, 42, 0.06);
}
.btn-ghost {
  background: transparent;
}
.btn-primary {
  background: var(--primary);
  color: #fff;
  border-color: var(--primary);
}
.btn-primary:hover {
  background: var(--primary-600);
  border-color: var(--primary-600);
}
.btn-neutral {
  background: #f3f6fb;
  color: #0f172a;
  border-color: #e7edf6;
}
.btn-neutral:hover {
  background: #eef3fb;
}

/* Chips / pills */
.pill {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 10px;
  border-radius: 999px;
  background: #f3f6fb;
  border: 1px solid var(--border);
  color: var(--text);
  font-size: var(--fs-sm);
}
.pill.danger {
  background: #fee2e2;
  border-color: #fecaca;
  color: #7f1d1d;
}
.redflag {
  background: #fff0f0;
  border-color: #ffd4d4;
  color: #991b1b;
}

/* Urgency badge */
.urgency {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  border-radius: 999px;
  font-weight: 800;
  border: 1px solid transparent;
  width: max-content;
}
.urgency.vu {
  background: #fee2e2;
  color: #991b1b;
  border-color: #fecaca;
}
.urgency.u {
  background: #fff7ed;
  color: #9a3412;
  border-color: #fed7aa;
}
.urgency.s {
  background: #dcfce7;
  color: #166534;
  border-color: #bbf7d0;
}

/* Chip set */
.chips {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.chip {
  background: #f8fafc;
  border: 1px solid var(--border);
  border-radius: 999px;
  padding: 6px 10px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}
.chip-x {
  border: none;
  background: none;
  font-size: 16px;
  line-height: 1;
  cursor: pointer;
  color: #64748b;
}

.muted {
  color: var(--muted);
}
.error {
  color: var(--danger);
  font-size: var(--fs-xs);
  margin-top: 4px;
}

/* Stepper */
.stepper {
  margin: 8px 0 16px;
}
.progress {
  height: 4px;
  background: #e8eef7;
  border-radius: 999px;
  overflow: hidden;
}
.progress-fill {
  height: 100%;
  background: var(--primary);
  width: 0%;
}
.steps {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}
.dot {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background: #e8eef7;
  color: #3b82f6;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
}
.dot.active {
  background: var(--primary);
  color: #fff;
}
.dot.done {
  background: #22c55e;
  color: #fff;
}
.label {
  font-size: var(--fs-xs);
  color: var(--muted);
}
.label.active {
  color: var(--text);
  font-weight: 700;
}

/* Tabs */
.tabs {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}
.tab {
  padding: 8px 12px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: #fff;
  color: var(--text);
  cursor: pointer;
}
.tab.active {
  background: var(--primary);
  color: #fff;
  border-color: var(--primary);
}
.tab:hover {
  box-shadow: 0 2px 8px rgba(15, 23, 42, 0.06);
}

/* Prominent search bar */
.searchbar {
  display: flex;
  align-items: center;
  gap: 10px;
  background: #fff;
  border: 1px solid var(--border);
  padding: 10px 12px;
  border-radius: 12px;
  box-shadow: 0 3px 12px rgba(15, 23, 42, 0.04);
  margin-bottom: 12px;
  max-width: 560px;
}
.searchbar svg {
  color: #64748b;
  flex: 0 0 auto;
}
.search-input {
  border: none;
  outline: none;
  width: 100%;
  font-size: var(--fs-base);
  background: transparent;
}

/* Reasons grid – responsive auto-fit */
.reasons {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}
.reason {
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 12px;
  cursor: pointer;
  transition: box-shadow 0.15s ease, border-color 0.15s ease;
}
.reason:hover {
  box-shadow: 0 6px 16px rgba(15, 23, 42, 0.06);
}
.reason.active {
  border-color: var(--primary);
}
.reason .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}
.reason-title {
  font-weight: 700;
}
.reason-cat {
  color: var(--muted);
  font-size: var(--fs-xs);
}
.reason .hint {
  margin-top: 6px;
  color: #0f172a;
  background: #f5f8ff;
  border: 1px dashed #c7d2fe;
  padding: 6px 8px;
  border-radius: 8px;
}

/* Sticky bar */
.stickybar {
  margin-top: 12px;
  padding: 10px 12px;
  border-top: 1px solid var(--border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  background: #fff;
  border-radius: 0 0 var(--radius) var(--radius);
}

/* Segmented controls */
.seg {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}
.seg-btn {
  border: 1px solid var(--border);
  background: #fff;
  padding: 8px 12px;
  border-radius: 999px;
  cursor: pointer;
}
.seg-btn.on {
  background: var(--primary);
  color: #fff;
  border-color: var(--primary);
}

/* Symptom form – responsive auto-fit */
.form-grid {
  display: grid;
  gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}
.field {
  display: grid;
  gap: 6px;
}
.field-label {
  font-weight: 700;
}
.field-desc {
  font-size: var(--fs-xs);
}
.radio-row,
.check-row {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
/* bigger tap area for radios/checkbox labels */
.pad {
  padding: 4px 6px;
  border-radius: 8px;
}

/* Helpers */
.age-chip {
  padding: 0 6px;
}
.banner {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #fff7ed;
  border: 1px solid #fed7aa;
  color: #7c2d12;
  padding: 10px 12px;
  border-radius: 12px;
}
.preg-label {
  margin-left: auto;
}

/* Skeleton */
.skeleton-wrap {
  display: grid;
  gap: 12px;
}
.skeleton-row {
  height: 18px;
  background: linear-gradient(90deg, #eef2f6, #f7f9fc, #eef2f6);
  border-radius: 8px;
}
.skeleton-card {
  height: 120px;
  background: linear-gradient(90deg, #eef2f6, #f7f9fc, #eef2f6);
  border-radius: 12px;
}

/* Footer spacing */
.footer-row {
  margin-top: 20px;
}

/* Gender fieldset */
.fieldset {
  border: none;
  padding: 0;
  margin: 0;
}
.sex-row {
  margin-top: 6px;
  gap: 18px;
}
.sex-opt input {
  margin-right: 6px;
}

/* AI summary */
.ai-summary {
  margin-top: 6px;
  font-size: var(--fs-base);
  line-height: var(--lh);
}
